import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    region: new Array(4).fill('')
  },
  getters: {
    // {{ this.$store.getters.方法名 }}
    getLength(state) {
      return state.username.length;
    }
  },
  // 只能修改state的值，不能异步ajax请求
  mutations: {
    resetRegion(state) {
      state.region = new Array(4).fill('');
    },
    saveRegion(state, payLoad) {
      state.region = payLoad;
    }

  },
  // 1、可以异步ajax请求 2、提交的是mutations,不能直接变更状态
  actions: {
  },
  modules: {
  }
})
