import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/Index.vue'
// import BillList from '../views/BillList.vue'
// import ActivityList from '../views/ActivityList.vue'
// import ProductList from '../views/ProductList.vue'
// import ActivityDetail from '../views/ActivityDetail.vue'
// import ProductDetail from '../views/ProductDetail.vue'
// import MIndex from '../views/MIndex.vue'
// import Right from '../views/Right.vue'
// import Service from '../views/Cservice.vue'
// import Address from '../views/Address.vue'
// import Account from '../views/Account.vue'
// import Register from '../views/Register.vue'
// import Login from '../views/Login.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Index
  },
  {
    path: '/activitylist',
    // component: ActivityList
    component: () => import(/* webpackChunkName:"activityList" */'../views/ActivityList.vue')
  },
  {
    path: '/productlist',
    // component: ProductList
    component: () => import(/* webpackChunkName:"productList" */'../views/ProductList.vue')
  },

  {
    path: '/activitydetail/:aid',
    // component: ActivityDetail,
    props: true,
    component: () => import(/* webpackChunkName:"activityDetail" */'../views/ActivityDetail.vue')
  },
  {
    path: '/productdetail/:pid',
    // component: ProductDetail,
    props: true,
    component: () => import(/* webpackChunkName:"productDetail" */'../views/ProductDetail.vue')
  },
  {
    path: '/MIndex',
    // component: MIndex,
    meta: { requireAuth: true },
    component: () => import(/* webpackChunkName:"mIndex" */'../views/MIndex.vue')
  },
  {
    path: '/address',
    // component: Address,
    meta: { requireAuth: true },
    component: () => import(/* webpackChunkName:"address" */'../views/Address.vue')
  },
  {
    path: '/account',
    // component: Account,
    meta: { requireAuth: true },
    component: () => import(/* webpackChunkName:"account" */'../views/Account.vue')
  },
  {
    path: '/billlist',
    // component: BillList,
    meta: { requireAuth: true },
    component: () => import(/* webpackChunkName:"billList" */'../views/BillList.vue')
  },
  {
    path: '/rights',
    // component: Right
    component: () => import(/* webpackChunkName:"right" */'../views/Rights.vue')
  },
  {
    path: '/service',
    // component: Service
    component: () => import(/* webpackChunkName:"service" */'../views/Service.vue')
  },
  {
    path: '/register',
    // component: Register
    component: () => import(/* webpackChunkName:"register" */'../views/Register.vue')
  },
  {
    path: '/login',
    // component: Login
    component: () => import(/* webpackChunkName:"login" */'../views/Login.vue')
  },

  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(r => r.meta.requireAuth)) {
    if (localStorage.getItem('islogined')) {
      next();
    } else {
      router.push({ path: '/login', query: { path: to.fullPath } });
    }
  }
  else { next(); }

})
router.afterEach((to, from, next) => {
  window.scrollTo(0, 0);
})


export default router
