<template>
  <div>
    <!-- 顶部导航开始 -->
    <user-header></user-header>
    <mt-header class="header" fixed>
      <div slot="left" v-show="visible != false">
        <mt-button icon="back" @click="goBack"></mt-button>
      </div>
      <div slot="right" v-if="!this.isLogined">
        <router-link to="/login">登录</router-link>
        <router-link to="/register">注册</router-link>
      </div>
      <div slot="right" v-else>
        <a href="javascript:" @click="logout">欢迎{{ this.name }}，注销</a>
      </div>
    </mt-header>

    <!-- 顶部导航结束 -->
  </div>
</template>
<script>
import userHeader from "./userCenterHeader.vue";
import { bus } from "../main";
import { mapMutations } from "vuex";
export default {
  name: "my-header",
  props: ["visible"],
  components: { userHeader },
  data() {
    return {
      isLogined: Boolean,
      name: "",
    };
  },
  methods: {
    ...mapMutations(["resetRegion"]),
    logout() {
      this.resetRegion();
      localStorage.clear();
      this.$router.push({
        path: "/login",
        query: { path: this.$route.fullPath },
      });
      // this.$router.push("/login");
    },
    goBack() {
      // this.$emit("back", "ok");
      this.$router.back();
    },
  },
  computed: {
    // ...mapState(["username", "tel"]),
    // ...mapGetters(["getLength"]),
    // ...mapState({
    //   name: "username",
    //   phone: "tel",
    // }),
  },
  created() {
    bus.$on("getLoginInfo", (loginInfo) => {
      this.isLogined = loginInfo.isLogined;
      this.name = loginInfo.name;
      this.$emit("setNickName", this.name);
    });
    // var scrollTop =
    //   document.documentElement.scrollTop || document.body.scrollTop;
    // console.log(document.documentElement);
    // console.log(document.body);
    // document.documentElement.scrollTop = 0;
  },
};
</script>
<style scoped>
/* 顶部开始 */
.header a {
  color: #2281d5;
  margin-left: 0.6rem;
  font-size: 0.853rem;
}
.mint-header {
  background-color: #f2f2f2;
  height: 5.2rem;
  line-height: 6.2rem;
}
.mint-header .mint-button {
  color: #2281d5;
}
/* 顶部结束 */
</style>