<template></template>
<script>
import { bus } from "../main";
export default {
  name: "user-header",
  data() {
    return {
      isLogined: Boolean,
      name: "",
      tel: "",
      cardNum: "",
    };
  },
  methods: {
    getLoginInfo() {
      this.isLogined =
        localStorage.getItem("islogined") == "true" ? true : false;
      this.tel = localStorage.getItem("tel");
      this.name =
        localStorage.getItem("name") == null
          ? this.tel
          : localStorage.getItem("name");

      this.cardNum = localStorage.getItem("cardnum");
    },
  },
  mounted() {
    this.getLoginInfo();
    // if (!this.isLogined) {
    //   this.$messagebox.alert("请先登录！");
    //   this.$router.push("/login");
    // }
    bus.$emit("getLoginInfo", {
      name: this.name,
      isLogined: this.isLogined,
      tel: this.tel,
      cardNum: this.cardNum,
    });
  },
};
</script>