import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

// 创建全局事件bus,通过$emit触发事件/$on监听事件 来进行传递数据或者执行操作，在相应的子组件中需要通过  import {bus} from '../main' 引入bus
export const bus = new Vue();

// 注册全局组件header开始
import Header from './components/Header'
Vue.component(Header.name, Header);
// 注册全局组件header结束

// 注册全局组件用户中心头部userheader开始
import userHeader from './components/userCenterHeader'
Vue.component(userHeader.name, userHeader);
// 注册全局组件用户中心头部userheader结束

// 注册全局组件footer开始
import Footer from './components/Footer'
Vue.component(Footer.name, Footer);
// 注册全局组件footer结束

//引入axios开始// 
import axios from 'axios';
axios.defaults.baseURL = "";
Vue.prototype.axios = axios;
//引入axios结束//

//引入mint-ui开始//
import MintUI from 'mint-ui';
import 'mint-ui/lib/style.min.css';
import './assets/my-mint.css';
Vue.use(MintUI);
//引入mint-ui结束//

// qs(QeuryString)开始
import qs from 'qs';
Vue.prototype.qs = qs;
// qs(QeuryString)结束
Vue.config.productionTip = false


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
